 * Styles for the simple JupyterLab extension
 */

.jp-simple-extension-icon {
  background-size: 16px;
  margin-right: 8px;
}

.simple-sidebar-content {
  padding: 10px;
}

.simple-sidebar-content h2 {
  margin-top: 0;
  color: var(--jp-ui-font-color1);
}

.simple-sidebar-content p {
  color: var(--jp-ui-font-color2);
  margin-bottom: 20px;
}

.simple-sidebar-content button {
  width: 100%;
  padding: 8px;
  background-color: var(--jp-brand-color1);
  color: white;
  border: none;
  border-radius: 2px;
  cursor: pointer;
}

.simple-sidebar-content button:hover {
  background-color: var(--jp-brand-color0);
}

/* Markdown styling */
.markdown-content {
  font-family: var(--jp-ui-font-family);
  line-height: 1.5;
}

.markdown-content h1, 
.markdown-content h2, 
.markdown-content h3, 
.markdown-content h4, 
.markdown-content h5, 
.markdown-content h6 {
  margin-top: 1em;
  margin-bottom: 0.5em;
  font-weight: bold;
  color: var(--jp-ui-font-color0);
}

.markdown-content h1 {
  font-size: 1.5em;
  border-bottom: 1px solid var(--jp-border-color2);
  padding-bottom: 0.3em;
}

.markdown-content h2 {
  font-size: 1.3em;
}

.markdown-content h3 {
  font-size: 1.1em;
}

.markdown-content p {
  margin: 0.5em 0;
}

.markdown-content ul, 
.markdown-content ol {
  margin: 0.5em 0;
  padding-left: 1.5em;
}

.markdown-content li {
  margin: 0.25em 0;
}

.markdown-content a {
  color: var(--jp-content-link-color);
  text-decoration: none;
}

.markdown-content a:hover {
  text-decoration: underline;
}

.markdown-content blockquote {
  padding-left: 1em;
  margin-left: 0;
  border-left: 3px solid var(--jp-border-color2);
  color: var(--jp-ui-font-color2);
}

.markdown-content pre {
  background-color: var(--jp-cell-editor-background);
  padding: 0.75em;
  border-radius: 3px;
  overflow-x: auto;
}

.markdown-content code {
  font-family: var(--jp-code-font-family);
  background-color: var(--jp-cell-editor-background);
  padding: 0.2em 0.4em;
  border-radius: 3px;
  font-size: 0.9em;
}

.markdown-content pre code {
  padding: 0;
  background-color: transparent;
}

.markdown-content img {
  max-width: 100%;
}

/* Message container styles */
.message-container {
  flex-grow: 1;
  overflow-y: auto;
  padding: 10px;
  border-bottom: 1px solid #e0e0e0;
}

/* User message styles */
.user-message {
  margin-bottom: 10px;
  padding: 8px;
  border-radius: 5px;
  max-width: 80%;
  word-wrap: break-word;
  background-color: #e3f2fd;
  margin-left: auto;
}

/* Bot message styles */
.bot-message {
  margin-bottom: 10px;
  padding: 8px;
  border-radius: 5px;
  max-width: 80%;
  word-wrap: break-word;
  background-color: #f5f5f5;
  margin-right: auto;
}

/* Markdown indicator styles */
.markdown-indicator {
  position: absolute;
  top: 5px;
  right: 5px;
  font-size: 10px;
  background-color: var(--jp-brand-color1);
  color: white;
  padding: 2px 5px;
  border-radius: 3px;
  opacity: 0.7;
  z-index: 10;
}

/* Simple sidebar content */
.simple-sidebar-content {
  display: flex;
  flex-direction: column;
  height: 100%;
}

/* Input container styles */
.input-container {
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-top: 1px solid var(--jp-border-color1);
}

/* Input field styles */
.input-container textarea {
  min-height: 120px; /* 3x taller than default */
  max-height: 300px; /* Maximum height before scrolling */
  resize: vertical;
  padding: 8px;
  border: 1px solid var(--jp-border-color1);
  border-radius: 4px;
  font-family: var(--jp-code-font-family);
  font-size: var(--jp-code-font-size);
  line-height: 1.5;
  background-color: var(--jp-layout-color1);
  color: var(--jp-content-font-color1);
}

.input-container textarea:focus {
  outline: none;
  border-color: var(--jp-brand-color1);
  box-shadow: 0 0 0 1px var(--jp-brand-color1);
}

/* Control container styles */
.controls-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}

/* Toggle container styles */
.toggle-container {
  display: flex;
  align-items: center;
}

/* Action buttons container */
.action-buttons-container {
  display: flex;
  gap: 2px;
}

/* Action button styles */
.action-button {
  margin: 0 2px;
  padding: 2px 8px;
}

/* Input actions container */
.input-actions-container {
  display: flex;
  justify-content: flex-end;
  margin-top: 5px;
}

/* Send button styles */
.send-button {
  padding: 5px 10px;
}

/* 
 * Styles for the JupyterLab AI Assistant extension
 */

/* Title container styles */
.title-container {
  padding: 10px;
  border-bottom: 1px solid var(--jp-border-color2);
  margin-bottom: 5px;
}

/* Chat title input */
.chat-title-input {
  width: 100%;
  padding: 5px;
  font-weight: bold;
  border: 1px solid transparent;
  border-radius: 3px;
  background-color: transparent;
  color: var(--jp-ui-font-color0);
}

.chat-title-input:hover, .chat-title-input:focus {
  border-color: var(--jp-border-color1);
  background-color: var(--jp-layout-color1);
  outline: none;
}

/* Top actions container */
.top-actions-container {
  display: flex;
  padding: 5px 10px;
  gap: 10px;
  border-bottom: 1px solid var(--jp-border-color2);
}

/* History container */
.history-container {
  flex-grow: 1;
  overflow-y: auto;
  padding: 10px;
}

/* History item */
.history-item {
  padding: 10px;
  margin-bottom: 8px;
  border-radius: 5px;
  cursor: pointer;
  background-color: var(--jp-layout-color1);
  border: 1px solid var(--jp-border-color2);
  transition: all 0.2s ease;
}

.history-item:hover {
  background-color: var(--jp-layout-color2);
}

.history-item.active {
  border-left: 3px solid var(--jp-brand-color1);
}

/* History title */
.history-title {
  font-weight: bold;
  margin-bottom: 5px;
  color: var(--jp-ui-font-color0);
}

/* History preview */
.history-preview {
  font-size: 0.9em;
  color: var(--jp-ui-font-color2);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Empty history message */
.empty-history-message {
  text-align: center;
  padding: 20px;
  color: var(--jp-ui-font-color2);
  font-style: italic;
}

/* Add any additional css rules here */

/* Chat message containers */
.simple-sidebar-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

.message-container {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
}

.input-container {
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-top: 1px solid var(--jp-border-color1);
}

/* Message styling */
.user-message, .bot-message {
  margin-bottom: 15px;
  padding: 10px;
  border-radius: 8px;
  position: relative;
  overflow: hidden;
}

.user-message {
  background-color: var(--jp-layout-color2);
  color: var(--jp-content-font-color1);
  align-self: flex-end;
}

.bot-message {
  background-color: var(--jp-layout-color1);
  border: 1px solid var(--jp-border-color2);
  color: var(--jp-content-font-color1);
}

/* Markdown content styling */
.markdown-content {
  overflow-wrap: break-word;
  word-break: break-word;
}

/* Code block styling */
.streaming-content {
  font-family: var(--jp-code-font-family);
  white-space: pre-wrap;
  overflow-wrap: break-word;
  word-break: break-word;
  background-color: var(--jp-layout-color0);
  padding: 8px;
}

.markdown-content pre {
  background-color: var(--jp-cell-editor-background);
  border-radius: 4px;
  margin: 8px 0;
  padding: 10px;
  overflow-x: auto;
}

.markdown-content code {
  font-family: var(--jp-code-font-family);
  background-color: var(--jp-cell-editor-background);
  padding: 2px 4px;
  border-radius: 3px;
}

.markdown-content pre code {
  background-color: transparent;
  padding: 0;
  display: block;
}

/* Table styling */
.markdown-content table {
  border-collapse: collapse;
  margin: 10px 0;
  width: 100%;
}

.markdown-content th {
  background-color: var(--jp-layout-color2);
  border: 1px solid var(--jp-border-color2);
  padding: 5px 10px;
  text-align: left;
}

.markdown-content td {
  border: 1px solid var(--jp-border-color2);
  padding: 5px 10px;
  text-align: left;
}

/* Error message */
.error-message {
  color: var(--jp-error-color1);
  padding: 5px;
  font-weight: bold;
}

.command-menu-container {
  position: absolute;
  background: var(--jp-layout-color1);
  border: 1px solid var(--jp-border-color1);
  border-radius: 4px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  z-index: 1000;
  min-width: 200px;
}

.command-menu-item {
  padding: 8px 12px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.command-menu-item:hover {
  background: var(--jp-layout-color2);
}

.command-label {
  font-weight: bold;
  color: var(--jp-ui-font-color0);
}

.command-description {
  font-size: 0.9em;
  color: var(--jp-ui-font-color2);
}

/* Keyboard shortcut visual indicator */
.keyboard-shortcut-indicator {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--jp-brand-color1);
  color: white;
  padding: 8px 16px;
  border-radius: 4px;
  font-size: 14px;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
  z-index: 10000;
}

.keyboard-shortcut-indicator.visible {
  opacity: 1;
}

/* Message action buttons */
.message-actions {
  position: absolute;
  top: 5px;
  right: 45px; /* Positioned right after the MD indicator */
  display: flex;
  gap: 4px;
  opacity: 0.7;
  transition: opacity 0.2s ease;
  z-index: 10;
}

.message-actions:hover {
  opacity: 1;
}

.bot-message {
  position: relative;
  background-color: var(--jp-layout-color1);
  border: 1px solid var(--jp-border-color2);
  color: var(--jp-content-font-color1);
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 8px;
  min-height: 50px;
}

.message-action-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  padding: 0;
  border: none;
  border-radius: 3px;
  background-color: var(--jp-layout-color2);
  color: var(--jp-ui-font-color1);
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.message-action-button:hover {
  background-color: var(--jp-layout-color3);
  color: var(--jp-brand-color1);
