QAAQ;EACR,SAAS;EACT,gCAAgC;EAChC,wCAAwC;EACxC,YAAY;EACZ,iBAAiB;EACjB,kBAAkB;EAClB,eAAe;EACf,UAAU;EACV,6BAA6B;EAC7B,oBAAoB;EACpB,cAAc;AAChB;;AAEA;EACE,UAAU;AACZ;;AAEA,2BAA2B;AAC3B;EACE,kBAAkB;EAClB,QAAQ;EACR,WAAW,EAAE,4CAA4C;EACzD,aAAa;EACb,QAAQ;EACR,YAAY;EACZ,6BAA6B;EAC7B,WAAW;AACb;;AAEA;EACE,UAAU;AACZ;;AAEA;EACE,kBAAkB;EAClB,yCAAyC;EACzC,yCAAyC;EACzC,oCAAoC;EACpC,aAAa;EACb,mBAAmB;EACnB,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,WAAW;EACX,YAAY;EACZ,UAAU;EACV,YAAY;EACZ,kBAAkB;EAClB,yCAAyC;EACzC,+BAA+B;EAC/B,eAAe;EACf,eAAe;EACf,yBAAyB;AAC3B;;AAEA;EACE,yCAAyC;EACzC,6BAA6B;AAC/B","sourcesContent":["/* \r\n * Styles for the simple JupyterLab extension\r\n */\r\n\r\n.jp-simple-extension-icon {\r\n  background-size: 16px;\r\n  margin-right: 8px;\r\n}\r\n\r\n.simple-sidebar-content {\r\n  padding: 10px;\r\n}\r\n\r\n.simple-sidebar-content h2 {\r\n  margin-top: 0;\r\n  color: var(--jp-ui-font-color1);\r\n}\r\n\r\n.simple-sidebar-content p {\r\n  color: var(--jp-ui-font-color2);\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.simple-sidebar-content button {\r\n  width: 100%;\r\n  padding: 8px;\r\n  background-color: var(--jp-brand-color1);\r\n  color: white;\r\n  border: none;\r\n  border-radius: 2px;\r\n  cursor: pointer;\r\n}\r\n\r\n.simple-sidebar-content button:hover {\r\n  background-color: var(--jp-brand-color0);\r\n}\r\n\r\n/* Markdown styling */\r\n.markdown-content {\r\n  font-family: var(--jp-ui-font-family);\r\n  line-height: 1.5;\r\n}\r\n\r\n.markdown-content h1, \r\n.markdown-content h2, \r\n.markdown-content h3, \r\n.markdown-content h4, \r\n.markdown-content h5, \r\n.markdown-content h6 {\r\n  margin-top: 1em;\r\n  margin-bottom: 0.5em;\r\n  font-weight: bold;\r\n  color: var(--jp-ui-font-color0);\r\n}\r\n\r\n.markdown-content h1 {\r\n  font-size: 1.5em;\r\n  border-bottom: 1px solid var(--jp-border-color2);\r\n  padding-bottom: 0.3em;\r\n}\r\n\r\n.markdown-content h2 {\r\n  font-size: 1.3em;\r\n}\r\n\r\n.markdown-content h3 {\r\n  font-size: 1.1em;\r\n}\r\n\r\n.markdown-content p {\r\n  margin: 0.5em 0;\r\n}\r\n\r\n.markdown-content ul, \r\n.markdown-content ol {\r\n  margin: 0.5em 0;\r\n  padding-left: 1.5em;\r\n}\r\n\r\n.markdown-content li {\r\n  margin: 0.25em 0;\r\n}\r\n\r\n.markdown-content a {\r\n  color: var(--jp-content-link-color);\r\n  text-decoration: none;\r\n}\r\n\r\n.markdown-content a:hover {\r\n  text-decoration: underline;\r\n}\r\n\r\n.markdown-content blockquote {\r\n  padding-left: 1em;\r\n  margin-left: 0;\r\n  border-left: 3px solid var(--jp-border-color2);\r\n  color: var(--jp-ui-font-color2);\r\n}\r\n\r\n.markdown-content pre {\r\n  background-color: var(--jp-cell-editor-background);\r\n  padding: 0.75em;\r\n  border-radius: 3px;\r\n  overflow-x: auto;\r\n}\r\n\r\n.markdown-content code {\r\n  font-family: var(--jp-code-font-family);\r\n  background-color: var(--jp-cell-editor-background);\r\n  padding: 0.2em 0.4em;\r\n  border-radius: 3px;\r\n  font-size: 0.9em;\r\n}\r\n\r\n.markdown-content pre code {\r\n  padding: 0;\r\n  background-color: transparent;\r\n}\r\n\r\n.markdown-content img {\r\n  max-width: 100%;\r\n}\r\n\r\n/* Message container styles */\r\n.message-container {\r\n  flex-grow: 1;\r\n  overflow-y: auto;\r\n  padding: 10px;\r\n  border-bottom: 1px solid #e0e0e0;\r\n}\r\n\r\n/* User message styles */\r\n.user-message {\r\n  margin-bottom: 10px;\r\n  padding: 8px;\r\n  border-radius: 5px;\r\n  max-width: 80%;\r\n  word-wrap: break-word;\r\n  background-color: #e3f2fd;\r\n  margin-left: auto;\r\n}\r\n\r\n/* Bot message styles */\r\n.bot-message {\r\n  margin-bottom: 10px;\r\n  padding: 8px;\r\n  border-radius: 5px;\r\n  max-width: 80%;\r\n  word-wrap: break-word;\r\n  background-color: #f5f5f5;\r\n  margin-right: auto;\r\n}\r\n\r\n/* Markdown indicator styles */\r\n.markdown-indicator {\r\n  position: absolute;\r\n  top: 5px;\r\n  right: 5px;\r\n  font-size: 10px;\r\n  background-color: var(--jp-brand-color1);\r\n  color: white;\r\n  padding: 2px 5px;\r\n  border-radius: 3px;\r\n  opacity: 0.7;\r\n  z-index: 10;\r\n}\r\n\r\n/* Simple sidebar content */\r\n.simple-sidebar-content {\r\n  display: flex;\r\n  flex-direction: column;\r\n  height: 100%;\r\n}\r\n\r\n/* Input container styles */\r\n.input-container {\r\n  display: flex;\r\n  flex-direction: column;\r\n  padding: 10px;\r\n  border-top: 1px solid var(--jp-border-color1);\r\n}\r\n\r\n/* Input field styles */\r\n.input-container textarea {\r\n  min-height: 120px; /* 3x taller than default */\r\n  max-height: 300px; /* Maximum height before scrolling */\r\n  resize: vertical;\r\n  padding: 8px;\r\n  border: 1px solid var(--jp-border-color1);\r\n  border-radius: 4px;\r\n  font-family: var(--jp-code-font-family);\r\n  font-size: var(--jp-code-font-size);\r\n  line-height: 1.5;\r\n  background-color: var(--jp-layout-color1);\r\n  color: var(--jp-content-font-color1);\r\n}\r\n\r\n.input-container textarea:focus {\r\n  outline: none;\r\n  border-color: var(--jp-brand-color1);\r\n  box-shadow: 0 0 0 1px var(--jp-brand-color1);\r\n}\r\n\r\n/* Control container styles */\r\n.controls-container {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  margin-bottom: 5px;\r\n}\r\n\r\n/* Toggle container styles */\r\n.toggle-container {\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n/* Action buttons container */\r\n.action-buttons-container {\r\n  display: flex;\r\n  gap: 2px;\r\n}\r\n\r\n/* Action button styles */\r\n.action-button {\r\n  margin: 0 2px;\r\n  padding: 2px 8px;\r\n}\r\n\r\n/* Input actions container */\r\n.input-actions-container {\r\n  display: flex;\r\n  justify-content: flex-end;\r\n  margin-top: 5px;\r\n}\r\n\r\n/* Send button styles */\r\n.send-button {\r\n  padding: 5px 10px;\r\n}\r\n\r\n/* \r\n * Styles for the JupyterLab AI Assistant extension\r\n */\r\n\r\n/* Title container styles */\r\n.title-container {\r\n  padding: 10px;\r\n  border-bottom: 1px solid var(--jp-border-color2);\r\n  margin-bottom: 5px;\r\n}\r\n\r\n/* Chat title input */\r\n.chat-title-input {\r\n  width: 100%;\r\n  padding: 5px;\r\n  font-weight: bold;\r\n  border: 1px solid transparent;\r\n  border-radius: 3px;\r\n  background-color: transparent;\r\n  color: var(--jp-ui-font-color0);\r\n}\r\n\r\n.chat-title-input:hover, .chat-title-input:focus {\r\n  border-color: var(--jp-border-color1);\r\n  background-color: var(--jp-layout-color1);\r\n  outline: none;\r\n}\r\n\r\n/* Top actions container */\r\n.top-actions-container {\r\n  display: flex;\r\n  padding: 5px 10px;\r\n  gap: 10px;\r\n  border-bottom: 1px solid var(--jp-border-color2);\r\n}\r\n\r\n/* History container */\r\n.history-container {\r\n  flex-grow: 1;\r\n  overflow-y: auto;\r\n  padding: 10px;\r\n}\r\n\r\n/* History item */\r\n.history-item {\r\n  padding: 10px;\r\n  margin-bottom: 8px;\r\n  border-radius: 5px;\r\n  cursor: pointer;\r\n  background-color: var(--jp-layout-color1);\r\n  border: 1px solid var(--jp-border-color2);\r\n  transition: all 0.2s ease;\r\n}\r\n\r\n.history-item:hover {\r\n  background-color: var(--jp-layout-color2);\r\n}\r\n\r\n.history-item.active {\r\n  border-left: 3px solid var(--jp-brand-color1);\r\n}\r\n\r\n/* History title */\r\n.history-title {\r\n  font-weight: bold;\r\n  margin-bottom: 5px;\r\n  color: var(--jp-ui-font-color0);\r\n}\r\n\r\n/* History preview */\r\n.history-preview {\r\n  font-size: 0.9em;\r\n  color: var(--jp-ui-font-color2);\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n}\r\n\r\n/* Empty history message */\r\n.empty-history-message {\r\n  text-align: center;\r\n  padding: 20px;\r\n  color: var(--jp-ui-font-color2);\r\n  font-style: italic;\r\n}\r\n\r\n/* Add any additional css rules here */\r\n\r\n/* Chat message containers */\r\n.simple-sidebar-content {\r\n  display: flex;\r\n  flex-direction: column;\r\n  height: 100%;\r\n  overflow: hidden;\r\n}\r\n\r\n.message-container {\r\n  flex: 1;\r\n  overflow-y: auto;\r\n  padding: 10px;\r\n}\r\n\r\n.input-container {\r\n  display: flex;\r\n  flex-direction: column;\r\n  padding: 10px;\r\n  border-top: 1px solid var(--jp-border-color1);\r\n}\r\n\r\n/* Message styling */\r\n.user-message, .bot-message {\r\n  margin-bottom: 15px;\r\n  padding: 10px;\r\n  border-radius: 8px;\r\n  position: relative;\r\n  overflow: hidden;\r\n}\r\n\r\n.user-message {\r\n  background-color: var(--jp-layout-color2);\r\n  color: var(--jp-content-font-color1);\r\n  align-self: flex-end;\r\n}\r\n\r\n.bot-message {\r\n  background-color: var(--jp-layout-color1);\r\n  border: 1px solid var(--jp-border-color2);\r\n  color: var(--jp-content-font-color1);\r\n}\r\n\r\n/* Markdown content styling */\r\n.markdown-content {\r\n  overflow-wrap: break-word;\r\n  word-break: break-word;\r\n}\r\n\r\n/* Code block styling */\r\n.streaming-content {\r\n  font-family: var(--jp-code-font-family);\r\n  white-space: pre-wrap;\r\n  overflow-wrap: break-word;\r\n  word-break: break-word;\r\n  background-color: var(--jp-layout-color0);\r\n  padding: 8px;\r\n}\r\n\r\n.markdown-content pre {\r\n  background-color: var(--jp-cell-editor-background);\r\n  border-radius: 4px;\r\n  margin: 8px 0;\r\n  padding: 10px;\r\n  overflow-x: auto;\r\n}\r\n\r\n.markdown-content code {\r\n  font-family: var(--jp-code-font-family);\r\n  background-color: var(--jp-cell-editor-background);\r\n  padding: 2px 4px;\r\n  border-radius: 3px;\r\n}\r\n\r\n.markdown-content pre code {\r\n  background-color: transparent;\r\n  padding: 0;\r\n  display: block;\r\n}\r\n\r\n/* Table styling */\r\n.markdown-content table {\r\n  border-collapse: collapse;\r\n  margin: 10px 0;\r\n  width: 100%;\r\n}\r\n\r\n.markdown-content th {\r\n  background-color: var(--jp-layout-color2);\r\n  border: 1px solid var(--jp-border-color2);\r\n  padding: 5px 10px;\r\n  text-align: left;\r\n}\r\n\r\n.markdown-content td {\r\n  border: 1px solid var(--jp-border-color2);\r\n  padding: 5px 10px;\r\n  text-align: left;\r\n}\r\n\r\n/* Error message */\r\n.error-message {\r\n  color: